import CardInfo from 'card-info';
//import { cardNumber } from 'card-validator/dist/card-number';
import { el, setChildren } from 'redom';
//import Navigo from 'navigo';
function cardCreate() {
    const div = el('div', { class: 'container' });

    const divLeft = el('div', { class: 'left' });

    const h1 = el('h1', { 
        class: 'text-center' 
    }, 'Bank Card');

    const cardWrapper = el('div', { class: 'card-wrapper' });
    const cardBack = el('div', { class: 'card-back' });


    const cardBackLine = el('div', { class: 'card-back-line' });
    const inputCvv = el('input', 
    {   type: 'text', 
        id: 'cvv',
        placeholder: '1234',
        maxLength: 4
    });

    const cardFront = el('div', { class: 'card-front' });
    const cardImg = el('img', { class: 'card-image' });
    const cardLogo = el('img', { class: 'card-logo'});
    const cardNumber = el('input', 
    {   type: 'text', 
        id: 'card-number',
        placeholder: '1234 5678 9101 1121',
        maxLength: 19,
    });
    const cardMonthYear = el('input', 
    {   type: 'text', 
        id: 'card-to-my',
        placeholder: '02/40',
        maxLength: 5
    });

    setChildren(cardFront, [
        cardImg,
        cardLogo,
        cardNumber,
        cardMonthYear
    ]);
    setChildren(cardBack, [
        cardBackLine,
        inputCvv
    ])
    setChildren(cardWrapper, [
        cardBack,
        cardFront
    ]);
    setChildren(divLeft, [
        h1,
        cardWrapper
    ]);
    setChildren(div, divLeft);
    setChildren(window.document.body, div);

    cardNumber.addEventListener("keyup", (e) => {
        if (!e.target.value) {
            cardNumber.innerText = "1234 5678 9101 1121";
        } else {
            const valuesOfInput = e.target.value.replaceAll(" ", "");
    
            if (e.target.value.length > 14) {
                e.target.value = valuesOfInput.replace(/(\d{4})(\d{4})(\d{4})(\d{0,4})/, "$1 $2 $3 $4");
                cardNumber.innerHTML = valuesOfInput.replace(/(\d{4})(\d{4})(\d{4})(\d{0,4})/, "$1 $2 $3 $4");
            } else if (e.target.value.length > 9) {
                e.target.value = valuesOfInput.replace(/(\d{4})(\d{4})(\d{0,4})/, "$1 $2 $3");
                cardNumber.innerHTML = valuesOfInput.replace(/(\d{4})(\d{4})(\d{0,4})/, "$1 $2 $3");
            } else if (e.target.value.length > 4) {
                e.target.value = valuesOfInput.replace(/(\d{4})(\d{0,4})/, "$1 $2");
                cardNumber.innerHTML = valuesOfInput.replace(/(\d{4})(\d{0,4})/, "$1 $2");
            } else {
                cardNumber.innerHTML = valuesOfInput
            }
        }
    })

    cardMonthYear.addEventListener("keyup", (e) => {
        if (!e.target.value) {
            cardMonthYear.innerHTML = "02/40";
        } else {
            const valuesOfInput = e.target.value.replace("/", "");
    
            if (e.target.value.length > 2) {
                e.target.value = valuesOfInput.replace(/^(\d{2})(\d{0,2})/, "$1/$2");
                cardMonthYear.innerHTML = valuesOfInput.replace(/^(\d{2})(\d{0,2})/, "$1/$2");
            } else {
                cardMonthYear.innerHTML = valuesOfInput;
            }
        }
    })

    inputCvv.addEventListener("keyup", (e) => {
        if (!e.target.value) {
            inputCvv.innerHTML = "123";
        } else {
            inputCvv.innerHTML = e.target.value;
        }
    })

    cardNumber.oninput = (event) => {
        let cardNum = cardNumber.value;
        if (cardNum.trim().length > 5) {
            let cardInfo = new CardInfo(cardNum.trim(), {
                bankLogosPath: '/node_modules/card-info/dist/banks-logos/',
                brandsLogosPath: '/node_modules/card-info/dist/brands-logos/'
            });
            cardImg.src = cardInfo.bankLogo;
            cardLogo.src = cardInfo.brandLogo;
            cardFront.style.backgroundColor = cardInfo.backgroundColor;
        }
    }
}

cardCreate();

